import { IncomingMessage, Trigger } from '../trigger-data.types';

export abstract class MessageTriggerParser {
  INNER_IDENTIFIER: string | null = null;

  shouldHandle = (message: IncomingMessage): boolean => {
    try {
      return this._shouldHandle(message);
    } catch (e) {
      return false;
    }
  };

  abstract _shouldHandle(message: IncomingMessage): boolean;

  abstract handle(message: IncomingMessage, targetId: string | null): Trigger;

  extractInner = (message: IncomingMessage): string[] => {
    return [];
  };
}
